<template>
	<v-container fluid>
		<div v-if="!loading">
			<v-row class="my-2 subtitle-2 secondary--text pl-12">
				Sales (in units) per months
			</v-row>
			<!--table head-->
			<v-divider />
			<v-row class="subtitle-2" no-gutters>
				<v-col class="tabCell text-left" cols="2">Products</v-col>
				<v-divider vertical />
				<v-col v-for="month in 12"
					class="tabCell text-center"
					:key="month">
					{{ months(month) }}
				</v-col>
				<v-divider vertical />
				<v-col class="tabCell text-center font-weight-bold">Total</v-col>
				<v-divider vertical />
				<v-col class="tabCell text-center">Production plan</v-col>
				<v-divider vertical />
				<v-col class="tabCell text-center">Inventories beginning</v-col>
				<v-divider vertical />
				<v-col class="tabCell text-right font-weight-bold">Inventories end</v-col>
			</v-row>
			<!--table content-->
			<div v-if="sales?.length > 0">
				<div v-for="item in sales" :key="item.id" class="body-2">
					<v-row class="my-1" no-gutters>
						<v-col class="text-left" cols="2">{{item.trademark?.name}} {{ item.model }}</v-col>
						<v-divider vertical />
						<v-col v-for="month in 12"
							class="text-center"
							:key="`${item.id} - ${month}`">
							{{ formatNumber(monthSale(month, item)) }}
						</v-col>
						<v-divider vertical />
						<v-col class="text-center font-weight-bold">{{ formatNumber(item.sales_units) }}</v-col>
						<v-divider vertical />
						<v-col class="text-center">{{ formatNumber(item.production_plan) }}</v-col>
						<v-divider vertical />
						<v-col class="text-center">{{ formatNumber(item.inventories_beginning) }}</v-col>
						<v-divider vertical />
						<v-col class="text-right font-weight-bold">{{ formatNumber(item.inventories_end) }}</v-col>
					</v-row>
					<v-divider />
				</div>
			</div>
			<v-row v-else justify="center" class="my-4 error--text">
				No data yet
			</v-row>
		</div>
		<div v-else class="text-center">
			<loader></loader>
		</div> 
	</v-container>
</template>
<script>
import { getMonth, localeNumber } from '../../../functions/common'
	import Loader from '../../../layouts/Loader.vue'
	
	export default {
		data () {
			return {
				panel: [0],
			}
		},
		computed: {
			loading () {
				return this.$store.getters.loading
			},
			locale() {
				return this.$store.getters.locale;
			},
			user() {
				return this.$store.getters.user;
			},
			game() {
				return this.$store.getters.game;
			},
			sales() {
				return this.$store.getters.sales_team;
			},
			monthSales() {
				return this.$store.getters.monthSales_team;
			}

		},
		methods: {
			formatNumber(num) {
				let res = 0
				if (num) {
					res = localeNumber(num, this.locale);
				}
				return res;
			},
			months(num) {
				return getMonth(num);
			},
			monthSale(month, item) {
				let res = 0;
				if (this.monthSales?.length > 0) {
					const matched = this.monthSales.filter((it) => it.month == month && it.productId == item.productId);
					if (matched) {
						matched.forEach((ite) => {
							res += ite.sales
						})
					}
				}
				return res
			}
		},
		components: {Loader},
		async mounted() {
			if (this.sales == null && (this.game && this.user)) {
				const name = `${this.user.name}_${this.game.id}`;
				const team = this.game.teams.find(te => te.name == name)

				const params = {
					gameId: this.game.id,
					round: this.game.round - 1,
					teamId: team.id,
				}
				try {
					//get last round sales 
					await this.$store.dispatch('fetchTeamSales', params);
				}
				catch (error) {
					await this.$store.dispatch('setError', error.message);
				}
			}
			if (this.monthSales?.length < 1 && (this.game && this.user)) {
				const name = `${this.user.name}_${this.game.id}`;
				const team = this.game.teams.find(te => te.name == name)

				const params = {
					gameId: this.game.id,
					round: this.game.round - 1,
					teamId: team.id,
				}
				try {
					//get last round sales 
					await this.$store.dispatch('fetchTeamMonthsales', params);
				}
				catch (error) {
					await this.$store.dispatch('setError', error.message);
				}
			} /* */
		}
	}
</script>