<template>
	<v-container fluid>
		<div v-if="!loading">
			<v-row class="my-2 subtitle-2 secondary--text pl-12">
				Sales by channels
			</v-row>
			<!--table head-->
			<v-divider />
			<v-row class="subtitle-2" no-gutters>
				<v-col class="tabCell text-left" cols="2">Channels</v-col>
				<v-col v-for="(item, i) in headers" :key="i" 
					class="tabCell" :class="item.style">
					{{ item.title }}
				</v-col>
			</v-row>
			<div v-if="online?.length > 0">
				<v-row class="my-2 subtitle-2 secondary--text pl-12">
					Online self operated channel
				</v-row>
				<div v-for="item in online" :key="item.id">
					<v-row class="body-2" no-gutters>
						<v-col class="text-left" cols="2">{{item.name}}</v-col>
						<v-col class="text-center">{{ item.stores }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.peak_market_coverage) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.peak_market_coverage * item.stores) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.investment_costs) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.operational_costs) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.sales_last) }}</v-col>
						<v-col class="text-center">{{ formatNumber(salesValue(item)) }}</v-col>
					</v-row>
					<v-divider />
				</div>
			</div>
			<div v-if="outlets?.length > 0">
				<v-row class="my-2 subtitle-2 secondary--text pl-12">
					Brick&mortar self operated channels
				</v-row>
				<div v-for="item in outlets" :key="item.id">
					<v-row class="body-2" no-gutters>
						<v-col class="text-left" cols="2">{{item.name}}</v-col>
						<v-col class="text-center">{{ item.stores }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.peak_market_coverage) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.peak_market_coverage * item.stores) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.investment_costs) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.operational_costs) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.sales_last) }}</v-col>
						<v-col class="text-center">{{ formatNumber(salesValue(item)) }}</v-col>
					</v-row>
					<v-divider />
				</div>
			</div>
			<div v-if="franchises?.length > 0">
				<v-row class="my-2 subtitle-2 secondary--text pl-12">
					Franchises
				</v-row>
				<div v-for="item in franchises" :key="item.id">
					<v-row class="body-2" no-gutters>
						<v-col class="text-left" cols="2">{{item.name}}</v-col>
						<v-col class="text-center">{{ item.stores }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.peak_market_coverage) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.peak_market_coverage * item.stores) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.investment_costs) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.operational_costs) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.sales_last) }}</v-col>
						<v-col class="text-center">{{ formatNumber(salesValue(item)) }}</v-col>
					</v-row>
					<v-divider />
				</div>
			</div>
			<div v-if="marketplaces?.length > 0">
				<v-row class="my-2 subtitle-2 secondary--text pl-12">
					Marketplaces
				</v-row>
				<div v-for="item in marketplaces" :key="item.id">
					<v-row class="body-2" no-gutters>
						<v-col class="text-left" cols="2">{{item.name}}</v-col>
						<v-col class="text-center">{{ item.stores }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.peak_market_coverage) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.peak_market_coverage * item.stores) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.investment_costs) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.operational_costs) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.sales_last) }}</v-col>
						<v-col class="text-center">{{ formatNumber(salesValue(item)) }}</v-col>
					</v-row>
					<v-divider />
				</div>
			</div>
			<div v-if="independent?.length > 0">
				<v-row class="my-2 subtitle-2 secondary--text pl-12">
					Independent chains
				</v-row>
				<div v-for="item in independent" :key="item.id">
					<v-row class="body-2" no-gutters>
						<v-col class="text-left" cols="2">{{item.name}}</v-col>
						<v-col class="text-center">{{ item.stores }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.peak_market_coverage) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.peak_market_coverage * item.stores) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.investment_costs) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.operational_costs) }}</v-col>
						<v-col class="text-center">{{ formatNumber(item.sales_last) }}</v-col>
						<v-col class="text-center">{{ formatNumber(salesValue(item)) }}</v-col>
					</v-row>
					<v-divider />
				</div>
			</div>
		</div>
		<div v-else class="text-center">
			<loader></loader>
		</div> 
	</v-container>
</template>
<script>
	import Loader from '../../../layouts/Loader.vue'
	import { localeNumber } from '../../../functions/common'
	export default {
		data () {
			return {
				panel: [0],
				headers: [
					{ title: 'Stores', style: 'text-center' },
					{ title: 'Peak market coverage (1 store)', style: 'text-center' },
					{ title: 'Peak market coverage (entire channel)', style: 'text-center' },
					{ title: 'Investments', style: 'text-center' },
					{ title: 'Operational costs', style: 'text-center' },
					{ title: 'Sales in units', style: 'text-center' },
					{ title: 'Sales in cash', style: 'text-center' },
				]
			}
		},
		computed: {
			loading() {
				return this.$store.getters.loading
				},
			locale() {
				return this.$store.getters.locale;
			},
			user() {
				return this.$store.getters.user;
			},
			game() {
				return this.$store.getters.game;
			},
			channels() {
				return this.$store.getters.channels;
			},
			sales() {
				return this.$store.getters.sales_team;
			},
			online() {
				let res = [];
				if (this.channels?.length > 0) {
					res = this.channels.filter((item) => item.type == 1);
				}
				return res;
			},
			outlets() {
				let res = [];
				if (this.channels?.length > 0) {
					res = this.channels.filter((item) => item.type == 2 || item.type == 3);
				}
				return res;
			},
			franchises() {
				let res = [];
				if (this.channels?.length > 0) {
					res = this.channels.filter((item) => item.type == 4);
				}
				return res;
			},
			independent() {
				let res = [];
				if (this.channels?.length > 0) {
					res = this.channels.filter((item) => item.type == 8);
				}
				return res;
			},
			marketplaces() {
				let res = [];
				if (this.channels?.length > 0) {
					res = this.channels.filter((item) => item.type == 7);
				}
				return res;
			},
		},
		methods: {
			formatNumber(num) {
				let res = 0
				if (num) {
					res = localeNumber(num, this.locale);
				}
				return res;
			},
			salesValue(channel) {
				let res = 0;
				if (this.sales?.length > 0) {
					this.sales.forEach((item) => {
						for (let i = 0; i < item.channels.length; i++) {
							if (item.channels[i] == channel.name) {
								res += item.channels_sales_cash[i];
							}
						}
					})
				}
				return res;
			}
		},
		components: {Loader},
		async mounted() {
			if (this.channels == null && (this.game && this.user) && this.game.round > 0) {
				const name = `${this.user.name}_${this.game.id}`;
				const team = this.game.teams.find(te => te.name == name)

				const params = {
					gameId: this.game.id,
					round: this.game.round - 1,
					teamId: team.id,
				}
				try {
					//get last round sales 
					await this.$store.dispatch('fetchTeamChannels', params);
				}
				catch (error) {
					await this.$store.dispatch('setError', error.message);
				}
			}

			if (this.sales == null && (this.game && this.user) && this.game.round > 0) {
				const name = `${this.user.name}_${this.game.id}`;
				const team = this.game.teams.find(te => te.name == name)

				const params = {
					gameId: this.game.id,
					round: this.game.round - 1,
					teamId: team.id,
				}
				try {
					//get last round sales 
					await this.$store.dispatch('fetchTeamSales', params);
				}
				catch (error) {
					await this.$store.dispatch('setError', error.message);
				}
			}
		}
	}
</script>