<template>
	<v-container fluid class="pa-0">
		<div v-if="!loading">
			<v-row class="body-2 font-weight-bold ml-12 my-4">
				<span class="ml-4">team: {{ user.name }}</span>
				<span class="ml-4">current round: {{ game?.round }}</span>
			</v-row>
			<v-divider></v-divider>

			<v-tabs v-model="tab" height="35"
				background-color="primary"
				fixed-tabs
				slider-color="accent"
				dark>
				<v-tab :to="link(item)" ripple
					class="text-capitalize font-weight-bold"
					v-for="item in items"
					:key="item">
					{{ item }}
				</v-tab>
			</v-tabs>
			<v-tabs-items v-model="tab" continuous>
				<v-tab-item value="/team/reports/sales">
					<sales></sales>
				</v-tab-item>
				<v-tab-item value="/team/reports/retailChannels">
					<retail-channels></retail-channels>
				</v-tab-item>
				<v-tab-item value="/team/reports/segmentResults">
					<segment-results></segment-results>
				</v-tab-item>
				<!-- <v-tab-item value="/team/reports/finances">
					<financial-summary></financial-summary>
				</v-tab-item> -->
			</v-tabs-items>
		</div>
		<div v-else justify="center">
			<loader />
		</div>
	</v-container>
</template>
<script>
	import Sales from './reports/Sales.vue'
	import RetailChannels from './reports/RetailChannels.vue'
	import SegmentResults from './reports/SegmentResults.vue'
	//import FinancialSummary from './reports/FinancialSummary.vue'

	import Loader from '../../layouts/Loader.vue'

	export default {
		data () {
			return {
				tab: 0,
				items: ['Sales','Retail channels','Segment results'],
				valid: false,
				error: '',
				selected: null,
				numberRules: [v => /^[+]?[0-9]/.test(v) || 'it\'s expected number'],
			}
		},
		computed: {
			loading() {
				return this.$store.getters.loading;
			},
			user () {
				return this.$store.getters.user
			},
			game () {
				return this.$store.getters.game
			},
		},
		methods: {
			link(item) {
				const item_arr = item.split(' ')
				const first = item_arr[0].toLowerCase()
				let second = '';

				if (item_arr[1]) {
					second = item_arr[1][0].toUpperCase() + item_arr[1].substr(1)
				}
				//console.log(`/team/market/${first + second}`)				
				return `/team/reports/${first + second}`
			}
		},
		async mounted() {
			if (!this.game && this.user) {
				const game_id = this.user.email.match(/\d/g); //get gameId from the user email
				try {
					await this.$store.dispatch('fetchGame', game_id);
				}
				catch (error) {
					console.log(error.message)
				}
			}
		},
		components: {
			Sales,
			RetailChannels,
			SegmentResults,
			//FinancialSummary,
			Loader
		}
	}
</script>