<template>
	<v-container fluid>
		<div v-if="!loading">
			<v-row class="my-2 subtitle-2 secondary--text pl-12">
				Sales (in units) per segments
			</v-row>
			<!--table head-->
			<div v-if="market_segments?.length > 0">
				<v-divider />
				<v-row class="subtitle-2" no-gutters>
					<v-col cols="2" class="tabCell text-left">Products</v-col>
					<v-divider vertical />
					<v-col v-for="seg in market_segments" 
						:key="seg.id"
						class="tabCell text-center">
						{{ seg.segment.name }}
					</v-col>
					<v-divider vertical />
					<v-col class="tabCell text-center font-weight-bold">Total (year)</v-col>
				</v-row>
				<!--table content-->
				<div v-if="sales?.length > 0">
					<div v-for="product in sales" :key="product.id">
						<v-row class="body-2 my-1" no-gutters>
							<v-col cols="2" class="text-left">{{product.trademark.name}} - {{ product.model }}</v-col>
							<v-divider vertical />
							<v-col v-for="seg in market_segments" 
								:key="seg.id"
								class="text-center">
								{{ formatNumber(segmentSale(product, seg)) }}
							</v-col>
							<v-divider vertical />
							<v-col class="text-center font-weight-bold">{{ formatNumber(product.sales_units) }}</v-col>
						</v-row>
						<v-divider />
					</div>
				</div>
			</div>
		</div>
		<div v-else class="text-center">
			<loader></loader>
		</div> 
	</v-container>
</template>
<script>
	import Loader from '../../../layouts/Loader.vue'
	import { localeNumber } from '../../../functions/common'
	export default {
		data () {
			return {
				panel: [0]
			}
		},
		computed: {
			loading () {
				return this.$store.getters.loading
			},
			locale() {
			return this.$store.getters.locale;
			},
			user() {
				return this.$store.getters.user;
			},
			game() {
				return this.$store.getters.game;
			},
			monthSales() {
				return this.$store.getters.monthSales_team;
			},
			sales() {
				return this.$store.getters.sales_team;
			},
			market_segments() {
				return this.$store.getters.market_segments
			},
		},
		methods: {
			formatNumber(num) {
				let res = 0
				if (num) {
					res = localeNumber(num, this.locale);
				}
				return res;
			},
			segmentSale(product, segment) {
				let res = 0
				if (this.monthSales?.length > 0) {
					const sales = this.monthSales.filter((it) => it.productId == product.productId && it.segmentId == segment.segmentId);
					if (sales.length > 0) {
						res = sales.reduce((sum, item) => {
							return sum+= item.sales_normalized;
						}, 0)
					}
				}
				return res;
			}
			
		},
		components: {
			Loader
		},
		async mounted() {

			if (this.monthSales?.length < 1 && (this.game && this.user) && Number(this.game.round) > 0) {
				const name = `${this.user.name}_${this.game.id}`;
				const team = this.game.teams.find(te => te.name == name)

				const params = {
					gameId: this.game.id,
					round: this.game.round - 1,
					teamId: team.id,
				}
				try {
					//get last round sales 
					await this.$store.dispatch('fetchTeamMonthsales', params);
				}
				catch (error) {
					await this.$store.dispatch('setError', error.message);
				}
			}
			if (this.market_segments?.length < 1 && this.game) {

				const params = {
					gameId: this.game.id,
					round: this.game.round,
				}
				await this.$store.dispatch('fetchMarketSegments', params);
			}
			if (this.sales == null && (this.game && this.user) && this.game.round > 0) {
				const name = `${this.user.name}_${this.game.id}`;
				const team = this.game.teams.find(te => te.name == name)

				const params = {
					gameId: this.game.id,
					round: this.game.round - 1,
					teamId: team.id,
				}
				try {
					//get last round sales 
					await this.$store.dispatch('fetchTeamSales', params);
				}
				catch (error) {
					await this.$store.dispatch('setError', error.message);
				}
			}
		}
	}
</script>